.footer {
  width: 100%;
  padding: 3rem 0;
  background-color: rgb(19, 19, 19, 0.8);
}
.footer-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}
.left {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.location {
  display: flex;
  margin-bottom: 0.8rem;
}
.location p {
  line-height: 30px;
}
.right {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.social {
  margin-top: 1rem;
}
.pointer {
  cursor: pointer;
}
h4 {
  font-size: 1.2rem;
  padding-bottom: 0.6rem;
}
p {
  font-size: 1.2rem;
}
@media (max-width: 640px) {
  .footer-container {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 400px) {
  h4 {
    font-size: 0.7rem;
  }
}
